   draw(ctx){
       const map = new Image()

       map.addEventListener('load', ()=> {
        
        ctx.clearRect(0, 0, 800, 480)
       ctx.drawImage(
           map, 0, 0, 800, 480, 0, 0, 800, 480
       )})
       map.src = "src/assets/Tiles.png";
   } 
   
   obstacles(){
       let obstacles = [];
       
       this.tiles[1].filter((ele, i)=>{
           if(ele !== 0)obstacles.push(i)
       })
       
       return obstacles;
       

       //rowNumber = i / this.col + 1 => x
        //colNumber = i % this.col + 1 => y
   }

   getRow(pos){
       //takes in this.y
       //420 will be on row 26
       return Math.floor(pos / this.tileSize);
       
   }
    getCol(pos) {
        //takes in this.x
        //660 will be on col 41
        return Math.floor(pos / this.tileSize);

    }
    getIndex(posX, posY){
        let row = this.getRow(posY);
        let col = this.getCol(posX);
        return row * this.cols + col;
        
    }

    emptyTile(colX, rowY){
        let idx =  rowY * this.cols + colX;
        if(this.tiles[1][idx] === 0){
           
            return true
        } else{
            return false;
        }
    }



   //if dino is moving upwards, check if dino top collide with tile bottom
